import React from 'react';
import { formatPhoneNumber } from 'react-phone-number-input/input';
import { postRequest } from "./functions/fetch.js";
import { FormLayout, Input, Group, Button, PanelHeader, Panel, PanelHeaderBack,  Select, ScreenSpinner } from '@vkontakte/vkui';

const Editprofile = ({ fetchedUser, id, go }) => {
	const formatDate = (date) => {
		let newDate = date.slice(8, 10) + date.slice(4, 8) + date.slice(0, 4);
		return newDate
	}

	const requestURL = 'https://ambassador-todo.herokuapp.com/access/find'

	const [isLoading, setIsLoading] = React.useState(true);
	const [user, setUser] = React.useState();
	const [university, setUniversity] = React.useState();
	const [phone, setPhone] = React.useState();
	const [personalemail, setPersonalEmail] = React.useState();
	const [birthday, setBirthday] = React.useState();
	const [universitypostaladdress, setUniversityPostalAddress] = React.useState();
	const [rectorfullname, setRectorFullName] = React.useState();
	const [rectorpostaladdress, setRectorPostalAddress] = React.useState();
	const [statusinuniversity, setStatusInUniversity] = React.useState();
	const [facultyfull, setFacultyFull] = React.useState();
	const [facultyshortly, setFacultyShortly] = React.useState();
	const [specialty, setSpecialty] = React.useState();
	const [personalpostaladdress, setPersonalPostalAddress] = React.useState();
	const [clothingsize, setClothingSize] = React.useState();
	const [fullName, setFullName] = React.useState();
	const [fullNameLatin, setFullNameLatin] = React.useState();
	const [town, setTown] = React.useState();
	const [universityShortly, setUniversityShortly] = React.useState();
	const [fetch, setFetch] = React.useState(true);



	const onChangeUniversityShortly = (event) => {
		setUniversityShortly(event.target.value)
	}

	const onChangeFullNameLatin = (event) => {
		setFullNameLatin(event.target.value)
	}
	const onChangeFullName = (event) => {
		setFullName(event.target.value)
	}

	const onChangeUniversity = (event) => {
		setUniversity(event.target.value)
	}

	const onChangePhone = (event) => {
		setPhone('+7' + event.target.value)
	}

	const onChangePersonalEmail = (event) => {
		setPersonalEmail(event.target.value)
	}

	const onChangeBirthday = (event) => {
		setBirthday(formatDate(event.target.value))
	}

	const onChangeUniversityPostalAddress = (event) => {
		setUniversityPostalAddress(event.target.value)
	}

	const onChangeRectorFullName = (event) => {
		setRectorFullName(event.target.value)
	}

	const onChangeRectorPostalAddress = (event) => {
		setRectorPostalAddress(event.target.value)
	}

	const onChangeStatusInUniversity = (event) => {
		setStatusInUniversity(event.target.value)
	}

	const onChangeFacultyFull = (event) => {
		setFacultyFull(event.target.value)
	}

	const onChangeFacultyShortly = (event) => {
		setFacultyShortly(event.target.value)
	}

	const onChangeSpecialty = (event) => {
		setSpecialty(event.target.value)
	}

	const onChangePersonalPostalAddress = (event) => {
		setPersonalPostalAddress(event.target.value)
	}

	const onChangeClothingSize = (event) => {
		setClothingSize(event.target.value)
	}

	const onChangeTown = (event) => {
		setTown(event.target.value)
	}

	const onClickForm = () => {
		let body = JSON.stringify({
			_id: user._id,
			vkID: user.vkID,
			avatar: " ",
			achievements: " ",
			phoneNumber: phone,
			birthday: birthday,
			fullName: fullName,
			latinFullName: fullNameLatin,
			personalEmail: personalemail,
			town: town,
			university: university,
			universityShortly: universityShortly,
			universityPostalAddress: universitypostaladdress,
			rectorFullName: rectorfullname,
			rectorPostalAddress: rectorpostaladdress,
			statusInUniversity: statusinuniversity,
			facultyFull: facultyfull,
			facultyShortly: facultyshortly,
			specialty: specialty,
			personalPostalAddress: personalpostaladdress,
			clothingSize: clothingsize
		})
		postRequest('POST', 'https://ambassador-todo.herokuapp.com/access/update', body)

	}
	if (fetch) {
		if (fetchedUser != null) {
			const vkID = JSON.stringify({ "vkID": fetchedUser.id })
			postRequest('POST', requestURL, vkID)
				.then(data => {
					setUser(data[0])
					setIsLoading(false)
					setFetch(false)
				})
				.catch(err => console.log(err))
		}
	}

	if (isLoading === true) {
		return (
			<Panel id={id}>
				<div style={{ display: 'flex', alignItems: 'center', flexDirection: 'column' }}>
					<ScreenSpinner style={{ marginTop: '50%' }} />
				</div>
			</Panel>
		)
	}


	return (

		<Panel id={id}>

			<PanelHeader
				left={<PanelHeaderBack style={{ color: "#fc2c38" }} onClick={go} data-to="profile" onMouseUp={go} />}> </PanelHeader>
			<Group>
				<FormLayout>
					<Input onChange={onChangeFullName} placeholder={user.fullName} type="text" name="fullname" top="" required />
					<Input onChange={onChangePhone} placeholder={formatPhoneNumber(user.phoneNumber)} pattern="[0-9]{2}\.[0-9]{2}\.[0-9]{4}" type="text" name="phonenumber" top="" bottom="    8005553535 ( 7, +7, 8)" required />
					<Input onChange={onChangeFullNameLatin} placeholder={user.latinFullName} type="text" name="fullname" top=" " required />
					<Input onChange={onChangePersonalEmail} placeholder={user.personalEmail} type="text" name="email" top=" " required />
					<Input onChange={onChangeBirthday} placeholder={user.birthday} type="date" name="dateofbirth" top=" " required />
					<Input onChange={onChangeTown} placeholder={user.town} type="text" name="city" top="" required />
					<Input onChange={onChangeUniversity} placeholder={user.university} type="text" name="university" top=" " bottom=" " required />
					<Input onChange={onChangeUniversityShortly} placeholder={user.universityShortly} type="text" name="university" top=" " bottom=" " required />
					<Input onChange={onChangeUniversityPostalAddress} placeholder={user.universityPostalAddress} bottom="    " type="text" name="pochtavuz" top="  " required />
					<Input onChange={onChangeRectorFullName} placeholder={user.rectorFullName} type="text" name="fiorector" top=" " required />
					<Input onChange={onChangeRectorPostalAddress} placeholder={user.rectorPostalAddress} ype="text" name="emailrector" top="  " required />
					<Select onChange={onChangeStatusInUniversity} placeholder={user.statusInUniversity} bottom=",     " top="  " >
						<option value="1  ">1  </option>
						<option value="2  ">2  </option>
						<option value="3  ">3  </option>
						<option value="4  ">4  </option>
						<option value="1  ">1  </option>
						<option value="2  ">2  </option>
						<option value="1  ">1  </option>
						<option value="2  ">2  </option>
						<option value="3  ">3  </option>
						<option value="4  ">4  </option>
						<option value="5  ">5  </option>
						<option value="1  "></option>
						<option value=" "> </option>
					</Select>
					<Input onChange={onChangeFacultyFull} placeholder={user.facultyFull} bottom=" " type="text" name="facultatifull" top="" required />
					<Input onChange={onChangeFacultyShortly} placeholder={user.facultyShortly} bottom=" " type="text" name="facultatiless" top="" required />
					<Input onChange={onChangeSpecialty} placeholder={user.specialty} type="text" name="speciality" top="" required />
					<Input onChange={onChangePersonalPostalAddress} placeholder={user.personalPostalAddress} bottom="     " type="text" name="pochtaadress" top="   ( )" required />
					<Select onChange={onChangeClothingSize} placeholder={user.clothingSize} top="  ( )" >
						<option value="XS ">XS </option>
						<option value="S ">S </option>
						<option value="M ">M </option>
						<option value="L ">L </option>
						<option value="XL ">XL </option>
						<option value="XXL ">XXL </option>
						<option value="XXS ">XXS </option>
						<option value="XS ">XS </option>
						<option value="S ">S </option>
						<option value="M ">M </option>
						<option value="L ">L </option>
						<option value="XL ">XL </option>
					</Select>
					<Button style={{ backgroundColor: '#fc2c38' }} type='submit' size='xl' onClick={onClickForm} onMouseUp={go} data-to="profile"></Button>
				</FormLayout>
			</Group>

		</Panel>
	)
}


export default Editprofile;