import React from 'react';
import { postRequest } from "./functions/fetch.js"
import { FormLayout, Input, Group, Button, PanelHeader, Panel, Textarea, Select, PanelHeaderBack } from '@vkontakte/vkui';


const requestURL = 'https://ambassador-todo.herokuapp.com/event'
const userRequestURL = "https://ambassador-todo.herokuapp.com/access/find"

const AddEventVneshOff = ({ fetchedUser, id, go }) => {

	const formatDate = (date) =>{
		let newDate = date.slice(8,10) + date.slice(4,8) + date.slice(0,4);
		return newDate
		}

	const [user, setUser] = React.useState();
	const [nameEvent, setNameEvent] = React.useState();
	const [place, setPlace] = React.useState();
	const [date, setDate] = React.useState();
	//const [companyRole, setCompanyRole] = React.useState();
	const [links, setLinks] = React.useState();
	const [participants, setParticipants] = React.useState();
	const [eventType, SetEventType] = React.useState();
	const [description, SetDescription] = React.useState();
	const [notes, SetNotes] = React.useState();
	const [callback, setCallback] = React.useState();
	const [fetch, setFetch] = React.useState(true);


if (fetch){
	if (fetchedUser != null) {
		const vkID = JSON.stringify({ "vkID": fetchedUser.id })
		postRequest('POST', userRequestURL, vkID)
			.then(data => {
				setUser(data[0])
				setFetch(false)
			})
			.catch(err => console.log(err))
	}
}
	const onChangeNameEvent = (event) => {
		setNameEvent(event.target.value)
	}

	const onChangePlace = (event) => {
		setPlace(event.target.value)
	}

	const onChangeDate = (event) => {
		setDate(formatDate(event.target.value))
	}
	const onChangeCallback = (event) => {
		setCallback(event.target.value)
	}

	const onChangeParticipants = (event) => {
		setParticipants(event.target.value)
	}

	const onChangeLinks = (event) => {
		setLinks(event.target.value)
	}
	const onChangeEventType = (event) => {
		SetEventType(event.target.value)
	}

	const onChangeDescription = (event) => {
		SetDescription(event.target.value)
	}

	const onChangeNotes = (event) => {
		SetNotes(event.target.value)
	}

	const onClickForm = () => {
		let body = JSON.stringify({
			participationForm: '',
			eventForm: '',
			nameEvent: nameEvent,
			eventPlace: place,
			date: date,
			eventType: eventType,
			description: description,
			//companyRole: companyRole,
			participants: participants,
			participantsCallback: callback,
			uploadsLinks: '',
			publicationLinks: links,
			notes: notes,
			ambassador: user.fullName,
			university: user.university
		})
		postRequest('POST', requestURL, body)
			.catch(err => console.log(err))

	}

	return (

		<Panel id={id}>

			<PanelHeader
				left={<PanelHeaderBack style={{ color: "#fc2c38" }} onClick={go} data-to="addeventsecondvnesh" />}>
				 </PanelHeader>
			<Group>
				<FormLayout>
					<Input onChange={onChangeNameEvent} type="text" name="name" top=" " required />
					<Input onChange={onChangeDate} type="date" name="data" top=" " required/>
					<Input onChange={onChangePlace} type="text" name="name" top=" " required/>
					
					<Select  onChange={onChangeEventType} top=" " placeholder=" " required>
						<option value=" "> </option>
						<option value="/-">/-</option>
						<option value="//">//</option>
						<option value=""></option>
                        <option value=""></option>
                        <option value="">  </option>
                        <option value="">  </option>
					</Select> 
					<Textarea onChange={onChangeDescription} name="description" top=" " required/>
					<Input onChange={onChangeCallback} type="text" name="participants" top=" "/>
					<Select  onChange={onChangeParticipants} top=" " placeholder=" " required>
						<option value="/-">1-29 </option>
						<option value="">30-99 </option>
						<option value="  .">100-299 </option>
						<option value="">300-999 </option>
						<option value="">1000  </option>
					</Select> 
					<Textarea onChange={onChangeLinks} name="links" top="  " />
					<Textarea onChange={onChangeNotes} name="notes" top="" />

					<Button style={{ backgroundColor: '#fc2c38' }} type='submit' size='xl' onClick={onClickForm} onMouseUp={go} data-to="events"></Button>
			
				</FormLayout>
			</Group>

		</Panel>
	)
}


export default AddEventVneshOff;